<template>
	<view class="gridBox">
		<view class="gridItem" v-for="item in source" :key="item.id" @click="handlerClick(item.id)">
			<image :src="item.picUrl" class="pic"></image>
			<view class="playCount">{{item.playCount | numberFormat}}</view>
			<view class="picName">{{item.name}}</view>
		</view>
	</view>
</template>

<script>
	export default {
		props: {
			/* 数据源 */
			source: {
				type: Array,
				default: []
			},
		},
		data() {
			return {

			};
		},
		methods: {
			handlerClick(val) {
				uni.navigateTo({
					url:`../../pages/songList/songList?id=${val}`
				})
			}
		},
		}
</script>

<style lang="scss">
	.gridBox {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;

		.gridItem {
			width: 220rpx;
			margin: 0 4rpx 20rpx;
			position: relative;
		}

		.pic {
			width: 220rpx;
			height: 220rpx;
		}

		.playCount {
			font-size: 24rpx;
			color: #fff;
			position: absolute;
			right: 10rpx;
			top: 4rpx;
			background-image: url("data:image/png;base64,");
			background-position: 0;
			background-repeat: no-repeat;
			background-size: 11px 10px;
			padding-left: 13px;
		}

		.picName {
			text-overflow: ellipsis;
			overflow: hidden;
			font-size: 26rpx;
			line-height: 1.2;
			display: -webkit-box;
			-webkit-line-clamp: 2;
			-webkit-box-orient: vertical;
		}
	}
</style>
